import React, { ReactElement } from "react";
import AuthForm, { Value, AuthFormProps } from "../components/AuthForm";
import * as Yup from "yup";
import { useParams } from "react-router-dom";
import axios from "../utils/axios";
interface Props {}

function ResetPassword({}: Props): ReactElement {
  const validate = (values) => {
    const errors = { Password: null, "Confirm Password": null, Email: null };
    if (!values.Email) {
      errors.Email = "Required";
    } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.Email)
    ) {
      errors.Email = "Invalid email";
    }

    if (!values["Password"]) {
      errors["Password"] = "Required";
    } else if (values["Password"].length < 8) {
      errors["Password"] = "Password is too short. Min lenght is 8";
    }
    if (!values["Confirm Password"]) {
      errors["Confirm Password"] = "Required";
    } else if (values["Password"].length < 8) {
      errors["Confirm Password"] = "Password is too short. Min lenght is 8";
    } else if (values["Confirm Password"] !== values["Password"]) {
      errors["Confirm Password"] = "Passwords do not match";
    }
    if (errors.Password || errors["Confirm Password"] || errors.Email) {
      return errors;
    }
    return null;
  };
  const validationSchema = Yup.object().shape({
    Password: Yup.string()
      .min(8, "Too Short!")
      .max(50, "Too Long!")
      .required("Required"),
    "Reset Password": Yup.string()
      .min(8, "Too Short!")
      .max(50, "Too Long!")
      .required("Required"),
  });

  const { token } = useParams();
  const submit = async (values: any) => {
    await axios.post("/api/reset-password", {
      email: values.Email,
      password: values.Password,
      password_confirmation: values["Confirm Password"],
      token,
    });
  };

  const authProps: AuthFormProps = {
    title: "Reset password",
    values: [
      {
        name: "Email",
        value: "",
        type: "",
        placeholder: "password",
      },
      {
        name: "Password",
        value: "",
        type: "password",
        placeholder: "password",
      },
      {
        name: "Confirm Password",
        value: "",
        type: "password",
        placeholder: "password",
      },
    ],
    validate,
    validationSchema,
    submit,
    submitText: "Reset password",
  };
  return (
    <div className="pt-20">
      <AuthForm {...authProps} />;
    </div>
  );
}

export default ResetPassword;
