import { ReactElement } from "react";
import { toast } from "react-toastify";
import { mutate } from "swr";
import FormContainer, {
  FormContainerProps,
} from "../../components/FormContainer";
import { InnerFormProps } from "../../components/InnerForm";
import axios from "../../utils/axios";
import { useUser } from "../../utils/dataFetching";
interface Props {}

function UserSettings({}: Props): ReactElement {
  const current_password = "current password";
  const password = "new password";
  const { user } = useUser();

  const submitProfile = (values) => {
    axios
      .put("/api/user/profile-information", {
        name: values.name,
      })
      .then(() => {
        toast.success("Successfully updated profile information", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      })
      .catch(() => {
        toast.error("Oops something went wrong", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
    mutate("/api/user");
  };
  const submitPasswords = (values) => {
    axios
      .put("/api/user/password", {
        current_password,
        password,
      })
      .then(() => {
        toast.success("Successfully updated password", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      })
      .catch(() => {
        toast.error("Oops something went wrong", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  };
  const validateProfile = (values) => {
    const errors = { name: null };
    if (!values.name) {
      errors.name = "Required";
    } else if (values.name.length < 1) {
      errors.name = "Name is too small";
    } else if (values.name.length > 255) {
      errors.name = "Name is too large";
    }
    if (errors.name) {
      return errors;
    }
    return null;
  };

  const validatePasswords = (values) => {
    const errors = { [current_password]: null, [password]: null };

    if (!values[current_password]) {
      errors[current_password] = "Required";
    } else if (values[current_password].length < 8) {
      errors[current_password] = "Minimum password lenght is 8";
    }
    if (!values[password]) {
      errors[password] = "Required";
    } else if (values[password].length < 8) {
      errors[password] = "Minimum password lenght is 8";
    }

    if (errors[current_password] || errors[password]) {
      return errors;
    }
    return null;
  };
  const updateProfile: InnerFormProps = {
    title: "Update profile",
    values: [{ name: "name", value: "", type: "text" }],
    validate: validateProfile,
    submit: submitProfile,
    submitText: "Update",
  };
  const updatePassword: InnerFormProps = {
    title: "Change password",
    values: [
      { name: "current password", value: "", type: "password" },
      { name: "new password", value: "", type: "password" },
    ],
    validate: validatePasswords,
    submit: submitPasswords,
    submitText: "Change",
  };
  const formContainer: FormContainerProps = {
    title: "User settings",
    forms: [updateProfile, updatePassword],
  };
  return (
    <div className="w-1/2">
      <div className="pt-20">
        <FormContainer {...formContainer} />
      </div>
    </div>
  );
}

export default UserSettings;
