import React, { ReactElement, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "../utils/axios";
import { mutate } from "swr";
import AuthForm, { AuthFormProps } from "../components/AuthForm";

interface Props {}
interface FormValues {
  email: string;
  password: string;
}

export default function LoginCard({}: Props): ReactElement {
  let history = useHistory();
  const [serverErrorMsg, setServerErrorMsg] = useState(null);
  // const { t, i18n } = useTranslation();

  const validate = (values) => {
    let errors = { email: "", password: "" };
    if (!values.email) {
      errors.email = "Verplicht";
    } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) {
      errors.email = "Ongeldige email";
    }
    if (!values.password) {
      errors.password = "Verplicht";
    } else if (values.password.length < 8) {
      errors.password = "Wachtwoord moet tenminste 8 karakters lang zijn";
    }

    if (errors.email || errors.password) {
      return errors;
    }
    return null;
  };

  const submit = async (values) => {
    // console.log("formikbag", FormikBag);
    await axios.get("/sanctum/csrf-cookie");
    try {
      await axios.post("/api/login", {
        email: values.email,
        password: values.password,
      });
      await mutate("/api/user");
      history.push("/");
    } catch (error) {
      try {
        setServerErrorMsg(error.response.data.errors.email[0]);
      } catch {
        setServerErrorMsg("Onjuiste inloggegevens");
      }
    }
  };
  const authProps: AuthFormProps = {
    title: "Sign in",
    values: [
      {
        name: "email",
        value: "",
        type: "",
        placeholder: "email",
      },
      {
        name: "password",
        value: "",
        type: "password",
        placeholder: "password",
      },
    ],
    validate,
    submit,
    submitText: "Sign in",
    bottomLink: { link: "/forgot-password", text: "Forgot password?" },
    serverErrorMsg,
  };
  return (
    <div className="pt-20">
      <AuthForm {...authProps} />;
    </div>
  );
}
