import React, { ReactElement, useEffect, useState } from "react";
import { useFormik, Form } from "formik";
import Input from "@notus-pro/react/Input";
import { useTranslation } from "react-i18next";
import Button from "@notus-pro/react/Button";
import * as Yup from "yup";
import { stringify } from "querystring";

export interface Value {
  name: string;
  value: string;
  type?: string;
  placeholder?: string;
  icon?: string;
}
export interface InnerFormProps {
  title: string;
  values: Array<Value>;
  validationSchema?: any;
  validate(values: any): any;
  submit(values: any, FormikBag?: any): any;
  submitText: string;
  styleModifier?: string;
  toggleModal?: boolean;
  bottomLink?: { text: string; link: string };
  serverErrorMsg?: string;
}

export default function InnerForm({
  title,
  values,
  validationSchema,
  validate,
  submit,
  submitText,
  styleModifier,
  toggleModal,
  bottomLink,
  serverErrorMsg,
}: InnerFormProps): ReactElement {
  let initialValues = {};
  useEffect(() => {
    values.forEach((value) => {
      initialValues[value.name] = value.value;
    });
  }, [values]);
  const formik = useFormik({
    initialValues: initialValues,
    validateOnChange: false,
    validateOnBlur: false,
    enableReinitialize: true,
    validate,
    onSubmit: async (values, FormikBag) => {
      const response = await submit(values);
      if (response) {
        Object.entries(response).forEach(([key, value]) => {
          formik.errors[key] = String(value);
        });
      }
    },
  });
  return (
    <>
      <div className="flex-auto px-4 lg:px-10 py-5 pt-0">
        <form onSubmit={formik.handleSubmit}>
          <h6 className="mt-6 mb-2 font-bold ">{title}</h6>
          <hr className="mb-6 border-b-1 border-blueGray-200" />
          {values &&
            values.map((value) => {
              return (
                <div className="relative w-full mb-2">
                  <label
                    className="block uppercase text-blueGray-500 text-sm
                           font-bold mb-2 ml-1"
                  >
                    {value.name}
                  </label>
                  {/* neg margin to override Input margin */}
                  <div className="-mb-2">
                    <Input
                      id={value.name}
                      name={value.name}
                      type={value.type}
                      onChange={formik.handleChange}
                      value={formik.values[value.name]}
                      placeholder={value.placeholder}
                    />
                  </div>
                  {formik.errors[value.name] ? (
                    <div className="text-red-500 text-xs">
                      {formik.errors[value.name]}
                    </div>
                  ) : (
                    <div className="pb-2"></div>
                  )}
                </div>
              );
            })}
          {serverErrorMsg ? (
            <div className="text-red-500 text-xs">{serverErrorMsg}</div>
          ) : (
            <div className="pb-2"></div>
          )}
          <div className={"text-right -mt-4"}>
            <Button color="pink" size="sm" type="submit">
              {submitText}
            </Button>
          </div>
        </form>
      </div>
    </>
  );
}
