import React, { ReactElement, useState, useEffect } from "react";
import { setLocale } from "yup";
import AuthForm, { Value, AuthFormProps } from "../components/AuthForm";
import GreenMesage from "../components/GreenMessage";
import axios from "../utils/axios";
import { toast } from "react-toastify";

interface Props {}
export default function ForgotPassword({}: Props): ReactElement {
  const validate = (values) => {
    const errors = { email: null };
    if (!values.email) {
      errors.email = "Required";
    } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) {
      errors.email = "Invalid email";
    }
    if (errors.email) {
      return errors;
    }
    return null;
  };

  const submit = async (values: any) => {
    await axios.get("/sanctum/csrf-cookie");
    toast.info("ðŸ“§ Sending email...", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
    axios
      .post("/api/forgot-password", {
        email: values.email,
      })
      .then((r) => {
        toast.success("Email sent!", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      })
      .catch((r) => {
        toast.error("Something went wrong", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  };
  const authProps: AuthFormProps = {
    title: "Forgot password",
    values: [
      {
        name: "email",
        value: "",
        type: "",
        placeholder: "email",
      },
    ],
    validate,
    submit,
    submitText: "Reset password",
    bottomLink: { link: "/login", text: "Sign in" },
  };
  return (
    <div className="pt-20">
      <AuthForm {...authProps} />
    </div>
  );
}
