import React, { ReactElement, useEffect, useState } from "react";
import { useFormik, Form } from "formik";
import Input from "@notus-pro/react/Input";
import { useTranslation } from "react-i18next";
import Button from "@notus-pro/react/Button";
import * as Yup from "yup";
import { stringify } from "querystring";

export interface Value {
  name: string;
  value: string;
  type?: string;
  placeholder?: string;
  icon?: string;
}
export interface AuthFormProps {
  title: string;
  values: Array<Value>;
  validationSchema?: any;
  validate(values: any): any;
  submit(values: any, FormikBag?: any): any;
  submitText: string;
  styleModifier?: string;
  toggleModal?: boolean;
  bottomLink?: { text: string; link: string };
  serverErrorMsg?: string;
}

export default function AuthForm({
  title,
  values,
  validationSchema,
  validate,
  submit,
  submitText,
  styleModifier,
  toggleModal,
  bottomLink,
  serverErrorMsg,
}: AuthFormProps): ReactElement {
  const [submitting, setSubmitting] = useState(false);
  const [formValues, setFormValues] = useState(values);
  let initialValues = {};
  useEffect(() => {
    setFormValues(values);
    values.forEach((value) => {
      initialValues[value.name] = value.value;
    });
  }, [values]);
  const formik = useFormik({
    initialValues: initialValues,
    validateOnChange: false,
    validateOnBlur: false,
    enableReinitialize: true,
    validate,
    onSubmit: async (values, FormikBag) => {
      setSubmitting(true);
      const response = await submit(values);
      setSubmitting(false);
      // console.log(response);
      if (response) {
        Object.entries(response).forEach(([key, value]) => {
          formik.errors[key] = String(value);
        });
      }
    },
  });
  return (
    <>
      <div className="flex content-center items-center justify-center">
        <div className="w-full lg:w-4/12 px-4">
          <div className="relative flex flex-col w-full mb-6 shadow-lg rounded-lg bg-white ">
            <div className="mb-0 px-6 py-6">
              <div className="text-center mb-3">
                <h6 className="text-blueGray-500 text-xl font-bold">{title}</h6>
                <hr className="mt-6 border-b-1 border-blueGray-200"></hr>
              </div>
            </div>
            <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
              <form onSubmit={formik.handleSubmit}>
                {values &&
                  values.map((value) => {
                    return (
                      <div className="relative w-full mb-2">
                        <label
                          className="block uppercase text-blueGray-500 text-sm
                           font-bold mb-2 ml-1"
                        >
                          {value.name}
                        </label>
                        {/* neg margin to override Input margin */}
                        <div className="-mb-2">
                          <Input
                            id={value.name}
                            name={value.name}
                            type={value.type}
                            onChange={formik.handleChange}
                            value={formik.values[value.name]}
                            placeholder={value.placeholder}
                          />
                        </div>
                        {formik.errors[value.name] ? (
                          <div className="text-red-500 text-xs">
                            {formik.errors[value.name]}
                          </div>
                        ) : (
                          <div className="pb-2"></div>
                        )}
                      </div>
                    );
                  })}
                {serverErrorMsg ? (
                  <div className="text-red-500 text-xs">{serverErrorMsg}</div>
                ) : (
                  <div className="pb-2"></div>
                )}

                <div className={"text-center mt-5"}>
                  <Button color="pink" fullWidth type="submit" className={""}>
                    {submitText}
                  </Button>
                </div>
              </form>
            </div>
          </div>

          {bottomLink && (
            <div className="flex flex-wrap mt-6 relative">
              <div className="w-1/2">
                <div className="text-blueGray-500 ml-2">
                  <a href={bottomLink.link}>{bottomLink.text}</a>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
}
